.feedback-form {
  max-width: 480px;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid rgba(0, 0, 0, .12);
  border-radius: 4px;
  flex-direction: column;
  margin: 0 auto;
  padding: 12px;
  display: flex;
  box-shadow: -10px -10px 10px #0129f0, 10px 10px 10px #ef0;
}

.feedback-form input, button {
  height: 58px;
  color: #f7f7f7;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 1.5rem;
  outline: none;
  padding: 2rem 2.25rem;
  font-family: inherit;
  font-size: 1.25rem;
  box-shadow: inset -5px -5px 12px #808486, inset 5px 5px 12px #1c2e82;
}

.feedback-form label {
  color: #f7f7f7;
  flex-direction: column;
  margin-bottom: 16px;
  font-style: italic;
  display: flex;
}

.feedback-form button {
  font: inherit;
  cursor: pointer;
  padding: 8px 12px;
}

input, textarea {
  font: inherit;
  padding: 8px;
}

textarea {
  color: #f7f7f7;
  resize: none;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 1.5rem;
  box-shadow: inset -5px -5px 12px #808486, inset 5px 5px 12px #1c2e82;
}

